<template>
<!-- v-show="isShow_jcjb" -->
    <div class="el-dialog__wrapper">
        <div class="cs-jcjb-dailog">
            <div class="el-dialog__header"><span class="el-dialog__title">确认{{content.button}}</span>
        </div>
        <div class="el-dialog__body cs-jcjb-dailog__body" >
            <div class="cs-jcjb-box__message">是否确认{{content.button}}？</div>
            <span>注意：发布成功后该操作将立即生效</span>

        </div>
        <div class="el-dialog__footer cs-jcjb-dailog__footer">
            <div class="dialog-footer">
                <el-button type="primary" :plain="true"><span>取消</span></el-button>
                <el-button type="primary" @click="todoclick"><span>确认{{content.button}}</span></el-button>
            </div>
        </div>
    </div>
</div>


</template>

<script>
export default {
    props: {
        content:{
            type:Object,
            default:function(){
                return {
                    button:"发布",
                    message:"注意：发布成功后该操作将立即生效",
                    fn:null,
                }
            }
        }
    },


    data: function () {
        return {

        }
    },
    methods: {
        todoclick(){
            this.content.fn();
            var Bus=global.eventHub;

            Bus.$emit("deteldata");
             Bus.$emit("tankuang");
            
        }
    },
    created: function () {

    },
    mounted:function() {

    }

}
</script>
<style>
.cs-jcjb-box__message{text-align: center;font-size: 16px;padding: 10px;}
.cs-jcjb-dialog__wrapper{z-index: 2222;}
.cs-jcjb-dailog{
    top:30%;
    right: 40%;
    position: absolute;
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 1px 3px rgba(0,0,0,.3);
    box-sizing: border-box;
}
.cs-jcjb-dailog__footer{
    bottom: 0px;
    position: absolute;
    right: 20px;
}
.cs-jcjb-dailog__body{
    margin-bottom: 60px;
    overflow-x: hidden;
    overflow-y:auto
}
</style>


